<template>
    <div class="words">
        <!-- <h1>{{tr}}</h1> -->
        <div class="words__filter">
            <input type="text" class="words__filter__text" v-model="textFilter" placeholder="英単語または意味で検索">
            <!-- <select name="PoS" id="Pos" class="words__fiter__Pos" v-model="PoS_Filter">
                <option class="words__filter__PoSValue" value="" disabled selected style="display:none">選択してください</option>
                <option class="words__filter__PoSValue" value="noun">名詞</option>
                <option class="words__filter__PoSValue" value="verb">動詞</option>
                <option class="words__filter__PoSValue" value="Adjective">形容詞</option>
                <option class="words__filter__PoSValue" value="default"></option>
            </select> -->
        </div>
            <transition-group name="cards__tra" tag="div" class="words__cardFlex" mode="in-out">
                <div class="words__card" v-for='item in wordData' :key='item' v-show="item.name.indexOf(textFilter) != -1 || item.description.indexOf(textFilter) != -1">
                    <h4 class="words__card__word">{{item.name}}</h4>
                    <p class="words__card__PoS"> [{{item.a}}]</p>
                    <p class="words__card__desc">{{item.description}}</p>
                </div>
            </transition-group>
            <div class="words__none" v-if="wordData.filter(x => x.name.indexOf(textFilter) != -1 || x.description.indexOf(textFilter) != -1).length == 0">
                <p>該当する単語が見つかりませんでした</p>
                <p>英単語で検索する場合は半角英文字で入力してください</p>
            </div>
    </div>
</template>

<script>

    export default {

    name: 'WordsView',

        data: function() {
            return {
                tr: 1234,
                textFilter: "",
                PoS_Filter: "",
            }
        },

        props: ['wordData'],
    }

</script>

<style scoped>

    .words {
        width: 100%;
        height: 100%;
        
        overflow-y: scroll;
        overflow-x: hidden;
    }

    
    .words::-webkit-scrollbar {
        width: 15px;
    }

    .words::-webkit-scrollbar-track {
        background-color: #e4e4e4;
        border-radius: 1px;
    }

    .words::-webkit-scrollbar-thumb {
        border-radius: 10px;
        border: 2px solid transparent;
        background-clip: content-box;
        background-color: #8e9cd7;
    }


    /* フィルター */
    .words__filter {
        padding: 15px 0.8%;
        border-bottom: dashed gray 1px;
        background-color: rgb(216 216 227);

        position: sticky;
        top: 0;

        z-index: 1;
    }

    .words__filter__text {
        background-color: whitesmoke;
        margin-right: 15px;
    }

    .words__filter__para {
        display: inline;
    }

    /* .words__fiter__Pos {
        background-color: whitesmoke;
        appearance: auto;
    }
    .words__filter__PoSValue {
        color: rgb(47 47 83);
        z-index: 10;
    }
    .words__filter__PoS:hover {
        color: rgb(47 47 83);
        background-color: white;
    } */

    .words__none {
        margin-top: 2rem;
    }
    .words__none p{
        text-align: center;
    }


    /* カード */
    .words__cardFlex {
        display: flex;
        justify-content: left;
        flex-wrap: wrap;
        width: 100%;
    }

    .words__card {
        width: fit-content;
        height: fit-content;
        min-width: 23%;
        margin: 15px 0.8%;

        background-color: white;
        border-radius: 5px;

        transition: all 0.5s ease;
    }

    .words__card__word {
        margin: 10px 5px 5px;
    }

    .words__card__PoS {
        font-size: 0.8rem;
        margin: 0 5px;
    }

    .words__card__desc {
        margin: 5px;
    }


    /* transition */

    .cards__tra-enter-from, 
    .cards__tra-leave-to {
        opacity: 0;
    }
    
    .cards__tra-enter-to,
    .cards__tra-leave-from {
        opacity: 1;
    }

    .cards__tra-leave-active {
        position: absolute;
        transition: transform 0s, opacity 0s;
    }

    .cards__tra-enter-active {
        transition: opacity 0.5s;
        transform: none;
    }


</style>
